.Tag {
    color:whitesmoke !important;
    position: absolute;
    text-decoration: none;
    line-height: 3vw;
    background-image: linear-gradient(tomato, tomato);
    background-size: 20px 0.15em;
    background-position-y: 100%;
    background-position-x: 0%;
    background-repeat: no-repeat;
}

.Tag1 {
    visibility:hidden;
    -webkit-animation: Tag1Visible 10s infinite ease-in-out;
            animation: Tag1Visible 10s infinite ease-in-out;
    color:whitesmoke;
} 

.Tag2 {
    visibility:hidden;
    -webkit-animation: Tag2Visible 10s infinite ease-in-out;
            animation: Tag2Visible 10s infinite ease-in-out;
    color:whitesmoke;
}

.Tag3 {
    visibility:hidden;
    -webkit-animation: Tag3Visible 10s infinite ease-in-out;
            animation: Tag3Visible 10s infinite ease-in-out;
    color:whitesmoke;
}

@-webkit-keyframes Tag1Visible {
    0% {visibility:visible;background-position-x: 0%;color:whitesmoke;}
    32% {visibility:visible;color:whitesmoke;}
    33% {visibility:hidden;background-position-x: 100%;}
    99% {visibility:hidden;}
    100% {visibility:visible;}
}

@keyframes Tag1Visible {
    0% {visibility:visible;background-position-x: 0%;color:whitesmoke;}
    32% {visibility:visible;color:whitesmoke;}
    33% {visibility:hidden;background-position-x: 100%;}
    99% {visibility:hidden;}
    100% {visibility:visible;}
}

@-webkit-keyframes Tag2Visible {
    0% {visibility:hidden;}
    33% {visibility:hidden;}
    34% {visibility:visible;background-position-x: 0%;color:whitesmoke;}
    65% {visibility:visible;color:whitesmoke;}
    66% {visibility:hidden;background-position-x: 100%;}
    100% {visibility:hidden;}
}

@keyframes Tag2Visible {
    0% {visibility:hidden;}
    33% {visibility:hidden;}
    34% {visibility:visible;background-position-x: 0%;color:whitesmoke;}
    65% {visibility:visible;color:whitesmoke;}
    66% {visibility:hidden;background-position-x: 100%;}
    100% {visibility:hidden;}
}

@-webkit-keyframes Tag3Visible {
    0% {visibility:hidden;}
    66% {visibility:hidden;}
    67% {visibility:visible;background-position-x: 0%;color:whitesmoke;}
    98% {visibility:visible;color:whitesmoke;}
    99% {visibility:hidden;background-position-x: 100%;}
}

@keyframes Tag3Visible {
    0% {visibility:hidden;}
    66% {visibility:hidden;}
    67% {visibility:visible;background-position-x: 0%;color:whitesmoke;}
    98% {visibility:visible;color:whitesmoke;}
    99% {visibility:hidden;background-position-x: 100%;}
}


