    10% {background-image:linear-gradient(126deg, tomato, whitesmoke);}
    15% {background-image:linear-gradient(144eg, tomato, whitesmoke);}
    20% {background-image:linear-gradient(162deg, tomato, whitesmoke);}
    25% {background-image:linear-gradient(180deg, tomato, whitesmoke);}
    30% {background-image:linear-gradient(198deg, tomato, whitesmoke);}
    35% {background-image:linear-gradient(216deg, tomato, whitesmoke);}
    40% {background-image:linear-gradient(234deg, tomato, whitesmoke);}
    45% {background-image:linear-gradient(252deg, tomato, whitesmoke);}
    50% {background-image:linear-gradient(270deg, tomato, whitesmoke);}
    55% {background-image:linear-gradient(288deg, tomato, whitesmoke);}
    60% {background-image:linear-gradient(306deg, tomato, whitesmoke);}
    65% {background-image:linear-gradient(324deg, tomato, whitesmoke);}
    70% {background-image:linear-gradient(342deg, tomato, whitesmoke);}
    75% {background-image:linear-gradient(360deg, tomato, whitesmoke);}
    80% {background-image:linear-gradient(18deg, tomato, whitesmoke);}
    85% {background-image:linear-gradient(36deg, tomato, whitesmoke);}
    90% {background-image:linear-gradient(54deg, tomato, whitesmoke);}
    95% {background-image:linear-gradient(72deg, tomato, whitesmoke);}
    100% {background-image:linear-gradient(90deg, tomato, whitesmoke);}
}




@keyframes RotatingGradient {
    0% {background-image:linear-gradient(90deg, tomato, whitesmoke);}
    5% {background-image:linear-gradient(108deg, tomato, whitesmoke);}
    10% {background-image:linear-gradient(126deg, tomato, whitesmoke);}
